import appDiyPage from "@/components/page-component/index/app-diy-page.vue";

export default {
    name: "app-module",

    components: {
        appDiyPage
    },

    data() {
        return {
            newPage: [],
            time: -1,
            current: 0,
            scrollLeft: 0,
        }
    },
    props: {
        homePages: {
            type: Object,
            default() {
                return {};
            }
        },
        scrollTop: {
            type: Number,
            default() {
                return 0;
            }
        },
        is_storage: Boolean,
        pageHide: Boolean,
        theme: Object,
        coupon_req: Boolean,
        is_required: Boolean,
        page_id: Number,
        /*********************************************/
        tabType: String,
        tabColor: String,
        textColor: String,
        tabBackground: String,
        list: Array,
        dIndex: Array,
    },

    computed: {
        scrollStyle() {
            let length = this.list.length;
            if (length < 5) {
                return {
                    width: `${100 / length}%`
                }
            } else {
                length = 5;
                return {
                    width: `${(100 / (length * 2 - 1)) * 2}%`,
                }
            }
        },
        tabNameFill() {
            return (index) => {
                if (this.tabType === 'filling') {
                    if (this.current === index) {
                        return {
                            backgroundColor: this.tabColor,
                            borderRadius: '32rpx',
                            padding: '0 24rpx',
                        }
                    }
                }
            };
        },
        tabNameStyle() {
            return (index) => {
                let color = 'auto';
                if (this.tabType === 'line') {
                    if (this.current === index) {
                        color = this.tabColor;
                    } else {
                        color = this.textColor;
                    }
                } else if (this.tabType === 'filling') {
                    if (this.current === index) {
                        color = this.tabBackground;
                    } else {
                        color = this.textColor;
                    }
                }

                return {color};
            }
        },
        newHomePages() {
            return (index) => {
                let { id, title, navs } = this.homePages;
                return {
                    'id': id,
                    'is_home_page': 0,
                    'show_navs': 0,
                    'title': title,
                    'navs': [{
                        'id': '0',
                        'name': navs[0]['name'],
                        'page_id': this.page_id,
                        'template': this.list[index],
                        'template_id': navs[0]['template_id'],
                    }],
                };
            }
        },
    },
    methods: {
        changeTab(index) {
            this.current = index;
            if (this.list.length > 4) {
                let num = parseFloat(this.scrollStyle['width']);
                this.scrollLeft = uni.getSystemInfoSync().windowWidth * (index * num - 50 + num / 2) / 100;
            }
        },

        buyProduct(data) {
            this.$emit('buyProduct', data);
        }
    },
}
